import React from 'react';
import {connect} from 'react-redux';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';
import {setAmountFrom, setAmountTo, setCurrencyRates, setCurrencyFrom, setCurrencyTo} from '../../store/calculate/actions'

const styles = theme => ({
  textField: {
    margin: 0,
    height: 62,
    width: 160,
    backgroundColor: "#BCBCBC",
    borderRadius: 5,
    paddingTop: 9,
    paddingLeft: 5,
    paddingRight: 5,
    fontWeight: "bold",
  },
});

class TextFields extends React.Component {

  handleChange = (event) => {
    this.props.onAmountFrom(event)
    this.props.calculateCurrency()
  }

  render() {
    const { classes, placeholder, id, readOnly} = this.props;

    return (
      <TextField
          id={id}
          type={'number'}
          label=""
          className={classes.textField}
          placeholder={placeholder}
          helperText=""
          fullWidth
          margin="normal"
          onChange={this.handleChange}
          InputLabelProps={{
            shrink: true,
          }}
          inputProps={{
            step: 1,
            min: 1,
            style:{fontSize: 22, paddingBottom: 6,},
            readOnly: readOnly,
          }}
        />
    );
}
}

TextFields.propTypes = {
classes: PropTypes.object.isRequired,
};

const mapStateToProps = (state) => {
  return {
    amountFrom: state.calculate.amountFrom,
    amountTo: state.calculate.amountTo,
    rates: state.calculate.rates
  }
}

const mapDispatchToProps = {
  setAmountFrom,
  setAmountTo,
  setCurrencyRates,
  setCurrencyFrom,
  setCurrencyTo
}

export default withStyles(styles)(TextFields); connect(mapStateToProps, mapDispatchToProps)(TextFields)
